export default {
	namespaced: true,
	state: () => ({
		errorMap: {
			'Name': '',
			'Mobile': '',
			'Email': '',
			'Corp': '/',
			"Area": '',
		},
		baseForm:{
			Name: '',
			Mobile: '',
			Email: '',
			Corp: '',
			Area: ''
		},
		clientForm:{
			Name: '',
			Mobile: '',
			Email: '',
			Corp: '',
			Area: ''
		},
		teamList:[],    //
	}),
	mutations: {
		updateClientForm(state, val){
			console.log('---store------',val)
			state.clientForm = {...state.clientForm,...val}
			console.log('---',state.clientForm)
		},
		updateTeamList(state, val){
			if(val && val.length ===0){
				state.teamList = []
			}else{
				state.teamList = [...state.teamList, ...val || []]
			}
		},
		cleanForm(state){
			state.clientForm=state.baseForm
		}
	},

	getters: {

	}
}
